.skillsWrapper {
//   margin: 0;
//   padding: 10vh 0 10vh 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 350px;
  perspective-origin: 50% 50%;
  color:white;
}

.skillsmain {
//   justify-content: center;
//   align-items: center;
//   display: grid;
//   grid-template-columns: 1.5fr 1.5fr 1.5fr 1fr;
//   grid-template-rows: 4.5fr 4.5fr 4.5fr;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.skillscontainer {
  width: 150px;
  height: 150px;
  margin: 20px;
  transform-style: flat;
  @media screen and (max-width: 960px) {
    width: 80px;
    height: 80px;
    margin: 5px;
    color: white;

  }
}

.skillscontainer > div {
  position: absolute;
  width: 150px;
  height: 150px;
  background: grey;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  will-change: transform;
  cursor: pointer;
  @media screen and (max-width: 960px) {
    width: 80px;
    height: 80px;
    margin: auto;
  }
}




#Skills_title, #Skills_sub_title {
    color:white;
    position: relative;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 5vw;
    font-family: 'Goldman', cursive;
    letter-spacing: 0.1em;
    transition: 0.3s;
    text-shadow: 1px 1px 0 rgba(128, 128, 128, 0.247), 1px 2px 0 grey, 1px 3px 0 grey, 1px 4px 0 grey,
      1px 5px 0 grey, 1px 6px 0 grey, 1px 7px 0 grey, 1px 8px 0 grey,
      5px 13px 15px black;
  }
  
  #Skills_title:hover {
    transition: 0.3s;
    transform: scale(1.1)translate(-45%, -45%);
    text-shadow: 1px -1px 0 grey, 1px -2px 0 grey, 1px -3px 0 grey,
      1px -4px 0 grey, 1px -5px 0 grey, 1px -6px 0 grey, 1px -7px 0 grey,
      1px -8px 0 grey, 5px -13px 15px black, 5px -13px 25px #808080;
  }

